.director {
  display: flex;
  flex-flow: row;
  margin-top: 2em;
  width: 50vw;
}
.director .left,
.director .right {
  margin: 1em;
  display: flex;
  flex-flow: column;
}

.director .director-name {
  display: flex;
  align-items: center;
  justify-content: center;
}
.director .director-films {
  display: flex;
  flex-flow: column;
}
.director a,
.director a:visited {
  color: rgb(41, 91, 218);
  text-decoration: none;
}
.director a:hover {
  color: rgb(146, 0, 19);
}

.director-thumbnail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 400px;
}
.director-thumbnail-image {
  object-fit: contain;
  max-width: 100%;
  min-width: 100%;
}
